'use strict';
let createdApi = '';
const apiCreator = {
  search: (page, query) => {
    return createdApi = `https://api.themoviedb.org/3/search/movie?api_key=4aa539255aa0c2506cf45806a15a8a0a&language=en-US&page=${page}&include_adult=false&query=${query}`;
  },
  popular: (page) => {
    return createdApi = `https://api.themoviedb.org/3/movie/popular?api_key=4aa539255aa0c2506cf45806a15a8a0a&language=en-US&page=${page}`;
  },
  image: function (link, w500 = false) {
    if (w500 === true) {
      return createdApi = `https://image.tmdb.org/t/p/w500/${link}`;
    }
    return createdApi = `https://image.tmdb.org/t/p/original/${link}`
  },
  movie: (id) => {
    return createdApi = `https://api.themoviedb.org/3/movie/${id}?api_key=e9f6322f77334e3f0406d6b8eabd79ce`;
    
  },
}
export {createdApi,apiCreator};
