'use strict';
import refs from '../index.js';
import {createdApi} from './apiCreator.js';
import filmSearcher from './filmSearcher.js';
import {apiData} from './showPromo.js';
let page = 1;
const loadMore = () => {
    // .log('refs :', refs);
    if (page >= apiData.total  || apiData.hasOwnProperty('errors')){
        refs.load_more_btn.style.display = 'none';
    }
    page += 1;
    // return page;
    filmSearcher();
    // console.log('filmSearcher :', filmSearcher);
    console.log('page :', page);
    return page;
}



const loadMoreReset = () =>{
    page = 1;
    refs.load_more_btn.style.display = 'block';
}
// load_more_btn.addEventListener('click', loadMore);
export {loadMore,page,loadMoreReset};
